@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Rubik', sans-serif;
}

html {
  background-color: var(--primary-color);
}

.button {
  text-decoration: none;
  display: block;
  border: none;
  margin: 2em auto;
  font-size: 1.2rem;
  padding: 1em 3em;
  background-color: var(--secondary-color);
  border-radius: var(--radius);
  color: white;
}

.button:hover {
  cursor: pointer;
  background-color: var(--secondary-color-dark);
}

.button:disabled {
  background-color: var(--grey);
  color: var(--dark-grey);
  cursor: not-allowed;
}

.input {
  display: block;
  margin: 0 auto;
  border: 1px solid black;
  border-radius: var(--radius);
  padding: 0.5em 1em;
  font-size: 1.2rem;
  min-width: 40%;
  max-width: 80%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  outline: none;
  height: 3rem;
}

.input:focus {
  border: 2px solid var(--secondary-color);
}

.input:focus::-webkit-input-placeholder {
  color: transparent;
}

.input:focus:-ms-input-placeholder {
  color: transparent;
}

.input:focus::placeholder {
  color: transparent;
}

.error {
  color: white;
  margin: 2em 4em;
  padding: 0.5em 0;
  text-align: center;
  border-radius: var(--radius);
  font-size: 1.1rem;
  background-color: var(--warning-color);
}

.success {
  color: black;
  margin: 2em 4em;
  padding: 0.5em 0;
  text-align: center;
  border-radius: var(--radius);
  font-size: 1.1rem;
  background-color: var(--success-color);
}

.half-page {
  width: 40vw;
  min-width: 300px;
  margin: 2em;
  background-color: white;
  border-radius: var(--radius);
}

:root {
  --primary-color: #202020;
  --secondary-color-hue: 311;
  --secondary-color: hsl(var(--secondary-color-hue), 100%, 30%);
  --secondary-color-dark: hsl(var(--secondary-color-hue), 100%, 22%);
  --secondary-color-light: hsl(var(--secondary-color-hue), 100%, 70%);
  --radius: 0.3rem;
  --page-clamp: clamp(300px, 65vw, 800px);
  --warning-color: rgb(255, 95, 95);
  --success-color: rgb(116, 209, 116);
  --grey: #bebebe;
  --dark-grey: #3a3a3a;
}

.navbar {
  background-color: var(--primary-color);
  padding-top: 1em;
}

.navbar__items {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 0 auto;
  width: var(--page-clamp);
  /* Align vertically */
  -webkit-align-items: center;
          align-items: center;
}

.navbar__title {
  color: white;
  text-decoration: none;
}

.navbar__links {
  display: -webkit-flex;
  display: flex;
}

.navbar__link {
  display: block;
  text-decoration: none;
  color: white;
  font-size: 1.1rem;
  margin: 0 1em;
}

.navbar__link:last-child {
  margin-right: 0;
}

.navbar__link:first-child {
  margin-left: 0;
}

/* Make navbar two lines if small enough */
@media (max-width: 550px) {
  .navbar__items {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
  }

  .navbar__links {
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    width: 100vw;
    margin-top: 1em;
  }
}

.navbar__link:hover,
.navbar__title:hover {
  cursor: pointer;
  color: var(--secondary-color-light);
}

.login {
  margin: 2em auto;
  margin-top: 2em;
  background-color: white;
  overflow: auto;
  width: var(--page-clamp);
  border-radius: var(--radius);
}

.login__title {
  margin: 1em auto;
  color: black;
  text-align: center;
}

.login__input {
  display: block;
  margin: 0 auto;
  border: 1px solid black;
  border-radius: var(--radius);
  padding: 0.5em 1em;
  font-size: 1.2rem;
  min-width: 40%;
  max-width: 80%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  outline: none;
  height: 3rem;
}

.login__input:focus {
  border: 2px solid var(--secondary-color);
}

.login__input:focus::-webkit-input-placeholder {
  color: transparent;
}

.login__input:focus:-ms-input-placeholder {
  color: transparent;
}

.login__input:focus::placeholder {
  color: transparent;
}

.login__error {
  margin: 2em 4em;
  padding: 0.5em 0;
  text-align: center;
  border-radius: var(--radius);
  font-size: 1.1rem;
  background-color: var(--warning-color);
}

.rooms-page {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.rooms-list {
}

.rooms-list__title {
  text-align: center;
  margin: 1em auto;
}

.rooms-list__list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  padding: 2em 1em;
  padding-top: 0;
}

.rooms-list__button {
  margin: 1em;
  font-size: 1.1rem;
}

.create-room {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.create-room__title {
  text-align: center;
  margin: 1em auto;
}

.room__messages {
  background-color: white;
  width: var(--page-clamp);
  margin: 0 auto;
  margin-top: 2em;
  border-radius: var(--radius);
  padding: 1em;
  overflow: auto;
  height: 50vh;
}

.room__message {
  margin-top: 0.5em;
  border-radius: var(--radius);
  padding: 0.5em 1em;
  background-color: var(--secondary-color);
  color: white;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  max-width: 70%;
  word-wrap: break-word;
}

.room__message--left {
  margin-top: 0;
  float: left;
  clear: both;
}

.room__message--right {
  background-color: var(--secondary-color-dark);
  float: right;
  clear: both;
}

.room__author--right:first-child {
  display: none;
}
.room__author--left {
  margin-top: 0.5em;
  float: left;
  clear: both;
}

.room__author--right {
  visibility: hidden;
  float: right;
  clear: both;
}

.room__send {
  width: var(--page-clamp);
  margin: 0 auto;
}

.room__input {
  margin: 0;
  margin-top: 1em;
}

.room__button {
  margin: 0;
  margin-top: 1em;
  padding: 0.5em 3em;
}

.room__title {
  margin-top: 1em;
  color: white;
  text-align: center;
}



